.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card {}

.card {
    height: 100%;
}

ul.list-inline {
    display: flex;
    flex-wrap: wrap;
}

li.list-inline-item {
    margin-bottom: 10px;
    margin-top: 10px;
}

img.card-img-top {
    height: 50%;
}

ul {
  list-style-type: none;
}

li {
    margin-bottom: 10px;
    margin-top: 10px;
}

.leftCol {
    background-color: #6495ED;
    height: 100vh;
}

.rightCol {
}

.center {
    margin: 10px;
    text-align: center;
}

img.imgProfile {
  width: 30px;
  height: 30px;
  float: left;
}
img {
  width: 100%;
  object-fit: cover;
}

.post {
  border: 1px solid LightGray;
  margin-bottom: 20px;
}







